@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,700&display=swap');

.App{
  margin: 0px;
  width: 100vw;
  font-family: 'Roboto', sans-serif;
  overflow-y: hidden;
  overflow-x: hidden;

}

.medium{
  font-size: 24px;
  margin-bottom: 5px;
}

.backboard{
  /*width:500px;
  position: absolute;
  height: 200px;
  background-color: #222831;

  transform: translate(50px,50px) rotate(-10deg);*/
  z-index: -1;
  width: 100vw;
  height: 700px;
  overflow-x: hidden;
  overflow-y: auto;

  position: absolute;
 
}
.band{
  width: 5000px;
  height: 500px;
  background-color: #222831;
  transform: translate(0px,-150px) skew(0deg,-5deg)
}

.Title{
  min-width: 500px;
  color:#eeeeee;
  font-size: 90px;
  font-weight: bold;
  padding:100px;
}
.description{
  padding-top: 30px;
  font-size: 24px;
}

.list::-webkit-scrollbar {
  display: none;
}
.list{
  display: flex;
  flex-direction: row;
}

.card{
  width: 480px;
  min-width: 480px;
  overflow: hidden;
  margin: 100px;
  margin-bottom: 50px;
  display:flex;
  flex-direction: column;
}

.angle{
  width: inherit;
  height: 265px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.thumbnail{
  width: inherit;
}

.bar{
  width: inherit;
  color: #393e46;
  margin-top: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
}
.decs{
  flex:1;
  padding-left: 10px ;
}

.name{
  font-size: 24px;
  margin-bottom: 5px;
  font-weight: 500;
}
.song{
  display: none;
  font-size: 16px;
}

.right{
  display: flex;
  justify-content: center;
  align-items: center;
}

.button{
  margin-right:20px;
  border-color: #ccc;
  border-width: 2px;
  border-style: solid;
  color: #ccc;
  border-radius: 10px;
  padding:10px;
  font-weight: bold;
}

.good{
  border-color: #32e0c4;
  background-color: #32e0c4;
  color: #fff;
}

.addPlayList{
  min-width: 600px;
  display: flex;
  flex-direction: column;
  margin-left: 100px;
}

.input_box{
  border-radius: 10px;
  padding:10px;
  border-color: #222831;
  border-style: solid;
  margin-bottom: 20px;
}

.input_name{
  width:500px;
}

.input_url{
  width:500px;
}

.sumbit{
  text-align: center;
  width: 100px;

  color: #222831;

  border-color: #222831;
  border-width: 3px;
  border-style: solid;
  padding:10px;
  font-weight: bold;

  border-radius: 10px;
}